import React from 'react';
import { Link } from 'gatsby';
import { string2html } from '../utils/string2html';
import camelCase from '../utils/camelCase';
import { formatReadingTime } from '../utils/readingTime';

const BlogPostTemplate = ({ meta, children }) => {
  const content =
    typeof children === 'string' ? string2html(children) : children;

  return (
    <main className="post">
      <article>
        <section className="post__header" role="contentinfo">
          <h1>{meta.title}</h1>
          <span>
            <time dateTime={meta.date}>{meta.date}</time>
            {` â€¢ ${formatReadingTime(meta.words)}`}
          </span>
          {meta.tags && meta.tags.length > 0 && (
            <ul className="tags">
              {meta.tags.map((t, i) => (
                <li>
                  <Link key={i} to={`/tags/${camelCase(t)}`}>
                    {t.toLowerCase()}
                  </Link>
                </li>
              ))}
            </ul>
          )}
        </section>

        <section className="card post__body">{content}</section>
      </article>
    </main>
  );
};

export default BlogPostTemplate;
